var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"report"},[_c('h1',{staticStyle:{"font-size":"35px","text-align":"center"}},[_vm._v("主题报告")]),_c('hr'),_c('div',{staticClass:"container"},[_c('div',{staticClass:"photo"},[_c('img',{attrs:{"src":require("../assets/images/1/11.png"),"alt":"陈世卿","align":"middle"}})]),_c('div',{staticClass:"content"},[_c('b',[_vm._v("特邀讲者：")]),_vm._v("陈世卿，第三脑（美国）研究院"),_c('br'),_c('b',[_vm._v("报告主题：")]),_vm._v("基于5G+脑科学+AI+智能超算的文创产业应用和人才培养"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("利用5G+脑科学+AI+智能超算制作高品质电影和5D虚拟主题公园。在10台专用、互连、分布式、并行和协作的超级计算机上制作一部3D/8K/120帧每秒的世界级数字电影；研究、开发世界上最先进的5D数字主题公园（5D-VTP）技术，利用5D特效（声音、图像、气味、触摸、运动）和第三大脑内容（在云上将人脑及其人工智能延伸进行动态耦合）增强娱乐效果，疗愈、改善和启迪从孩童到老年各个阶段人群的身心和精神，提升人们的良知、人性和道德水平，消除人类的贫穷、偏见、贪婪、仇恨、腐败和战争，加速全球合作、理解和创新，加快在全球范围内建立紧密联系、相互支持、美丽、清洁、生态友好、社会经济可持续的智慧型园林乡村或城市，维护人类与自然的和谐，维护世世代代的世界和平与繁荣。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("陈世卿，美国国家工程院院士、美国艺术与科学院院士、美国第三脑研究院院长、中国智慧城市建设投资联盟首席科学家、中国四川大学锦城学院人工智能学院院长、中国深圳人工智能与机器人研究院主任研究员、中国北京清华大学脑与认知科学研究院首席科学家、中国产业发展研究院人工智能研究院首席科学家。研发了世界上第一台用于科学研究的并行向量超级计算机；研发了使用通用型64-256 CPU的企业用高性能计算机；研发了世界上第一台128个刀片的超级计算机；研发了世界上第一个以互联网为基础、应用对应用、高效率、动态式企业协同作业软件TONBU；研发了世界上第一个以超级计算机为基础，全球联网形成类似电力网格的信息网格SCP；组织美国硅谷的非营利第三脑研究院Third Brain Research Institute，专注于世界前沿关于研究脑、保护脑、发展脑和延伸脑的研发项目，并筹建安全、可靠、高效的分布式/并行式/协作式超级网格云端平台SCP用以支持全球巨量、实时、遍布、无所不在的大数据存储和模拟运算。")])])]),_c('hr'),_c('h1',{staticStyle:{"font-size":"35px","text-align":"center"}},[_vm._v("文化计算与文化体验报告")]),_c('hr'),_c('div',{staticClass:"container"},[_c('div',{staticClass:"photo"},[_c('img',{attrs:{"src":require("../assets/images/2/21.png"),"alt":"陈世卿","align":"middle"}})]),_c('div',{staticClass:"content"},[_c('b',[_vm._v("特邀讲者：")]),_vm._v("赵海英，北京邮电大学"),_c('br'),_c('b',[_vm._v("报告主题：")]),_vm._v("文化计算在国家文化大数据体系建设中的实践与思考"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("随着现代信息和数字技术的发展，一门新的研究学科文化计算随之诞生。文化计算是利用社会计算、大数据、人工智能等技术与人文、历史等学科相互交叉融合，实现文化内容挖掘传播、推动数字人文研究，促进文化繁荣发展的技术手段。文化计算。2020年5月中宣部文改办下发的《关于做好国家文化大数据体系建设工作通知》中，提出建设文化大数据体系的八大任务，涉及到了数据的采集、存储、传输和应用，同时明确了文化大数据的中国文化遗产标本库、中华民族文化基因库以及中华文化素材库的建设，这些都体现了国家的意志。")]),_c('p',[_vm._v("文化计算离不开可计算的数据支撑。国家文化大数据体系建设就是要营造一种全社会共同推进文化发展的气氛，形成文化产业发展的循环经济。文化产业需要工具，更需要灵魂。文化计算一方面是文化产业的工具，从文化基因溯源看，文化计算也是文化产业发展的灵魂，通过文化计算的实践引申出新思考。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("赵海英，控制工程与科学专业博士学位，移动媒体与文化计算北京市重点实验室主任，文化计算与文化遗产数字化保护方向学术带头人。中国通信学会移动媒体与文化计算专业委员会秘书长，中国工业与应用数学学会几何设计与计算专委会委员、中国ACMSIGCHI 学会专业委员。首次完整提出文化基因的概念：是在特定地域和文化环境中形成的具有稳定性和继承性的基本信息模式，蕴含着文化固有的本质属性，并演绎着文化时空规律。总结了文化基因的三大特征，建立了文化基因的特征量化空间，为文化基因可量化、可计算、可分析提供理论依据。正在搭建文化基因语义标签网PatternNet，开展文化资源数字化理论与传承机理探寻的基础研究。")])])]),_c('hr'),_c('div',{staticClass:"container"},[_c('div',{staticClass:"photo"},[_c('img',{attrs:{"src":require("../assets/images/2/22.png"),"alt":"陈世卿","align":"middle"}})]),_c('div',{staticClass:"content"},[_c('b',[_vm._v("特邀讲者：")]),_vm._v("沈贵华，中国文物信息咨询中心"),_c('br'),_c('b',[_vm._v("报告主题：")]),_vm._v("文化计算赋能文化遗产价值挖掘与传承"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("中华文明博大精深，文物瑰宝灿若繁星，是历史中国源远流长的实物见证。文物蕴含着重要的历史、艺术和科学价值，让历史文物资源活起来，让文化网络空间亮起来，让民族精神内核传下去，是习近平总书记关心的大事；党的十八大以来，文博行业把握数字化时代重大机遇，部署文物资源数字化、文物管理信息化、文物展示网络化；面对人民群众日益高涨的精神需求，把计算机相关技术应用到文化遗产领域，探索其中的发展规律，揭示其内在关联并对其进行可视分析与呈现传播，有助于挖掘文化遗产内容的本质和传承优秀文化，讲清楚文化遗产蕴藏的历史传统和文化积淀，讲清楚中华民族独特的价值体系和精神追求，让文物成为民族认同、文化认同、国家认同的重要精神桥梁。借助文物数字互联，打通文物与科技、文物与民生、文物与教育、文物与旅游等行业的深度融合渠道，服务国家战略大局，助力社会繁荣进步。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("沈贵华，中国文物信息咨询中心政务信息部主任，中国文物学会理事、中国文物学会信息化专业委员会副主任委员。主要从事国家文物局电子政务、国家文物资源数据库、文物博物馆信息化等领域研究与实践。承担国家文物局电子政务平台规划、建设与运维；建设并运行国家文物资源数据库，为全国馆藏珍贵文物调查、第三次全国文物普查、第一次全国可移动文物普查等国家级重点文物数字化项目提供技术保障；承担部分省局及博物馆信息化规划编制工作，联合北京、山东、吉林、四川等省开展省域博物馆公众服务与博物馆大数据平台建设，推动普查数据公开共享、文物数字化传播，开展文物知识服务应用实践；在行业信息化总体规划、标准规范、平台建设、智慧博物馆与数字化保护等方面积累了丰富的经验。")])])]),_c('hr'),_c('div',{staticClass:"container"},[_c('div',{staticClass:"photo"},[_c('img',{attrs:{"src":require("../assets/images/2/23.png"),"alt":"陈世卿","align":"middle"}})]),_c('div',{staticClass:"content"},[_c('b',[_vm._v("特邀讲者：")]),_vm._v("桑新柱，北京邮电大学"),_c('br'),_c('b',[_vm._v("报告主题：")]),_vm._v("基于裸眼3D光场显示的文创体验"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("为了增强文化产品的创新体验，研究基于数字3D光场构建的裸眼3D光场显示交互呈现方式。模拟真实世界中人眼观看中物体或场景的呈现方式，是实现裸眼3D光场显示基本途径。介绍裸眼3D光场显示的基本原理，讨论裸眼3D光场显示的特点和基本实现方法，结合文化和数字化文物的3D光场创新呈现体验，给出了北京邮电大学在大尺寸裸眼3D光场显示方面开展的一些研究工作。实现了1.8m ×1.2 m幅面的光场显示，显示深度超过1m；实现了27英寸9216个视点光场信息的可交互全视差3D光场显示工作，水平和垂直视角45度；实现了65英寸的超过100度视角的3D光场显示，具有正确的空间危机几何遮挡关系;实现了基于LED的54英寸和162英寸的大尺寸、大视角的裸眼3D光场显示;实现了120°宽视角的裸眼3D光场显示和2m×2m、分辨率为23040×20650、视角为80°的裸眼3D光场显示。通过构建自由空间3D光场分布和高效计算，实现了多种形态自由空间空气成像的高清晰度动态裸眼3D光场显示。文物和文创产品的裸眼3D数字化光场呈现和交互，可提升人们对文物和文创作品的了解、认识和兴趣，有助于文化传承和宣传。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("桑新柱，北京邮电大学电子工程学院教授，博士生导师，副院长；信息光子学与光通信国家重点实验室研究三维显示与光电信息处理团队责任教授。2011年入选北京市科技新星计划和教育部新世纪优秀人才支持计划，2016年获得“北京市师德先锋”称号，2017年被评为北京市优秀教师，2018年被评为中国电子学会先进科技工作者，2019年获得教育部技术发明二等奖（排名第1）。兼任工信部电子科技委委员、中国光学学会理事、中国光学学会全息与光信息处理专业委员会副主任委员和秘书长、中国电子学会虚拟/增强现实技术与产业分会副主任委员、北京光学学会光学成像专委会主任委员等。")]),_c('p',[_vm._v("主要从事裸眼3D光场显示、全息显示级应用方面的研究工作。作为项目负责人承担国家级和省部级项目30余项，和20多家单位开展了应用合作，部分成果实现了产业转化，发表学术论文200余篇（被引用1500多次）；申请发明专利100多项，授权发明专利51项（转让2项，实施许可7项）；获得软件著作权11项。 ")])])]),_c('hr'),_c('div',{staticClass:"container"},[_c('div',{staticClass:"photo"},[_c('img',{attrs:{"src":require("../assets/images/2/24.png"),"alt":"陈世卿","align":"middle"}})]),_c('div',{staticClass:"content"},[_c('b',[_vm._v("特邀讲者：")]),_vm._v("林俊廷，青鸟新媒体艺术"),_c('br'),_c('b',[_vm._v("报告主题：")]),_vm._v("科技为文化插上翅膀，文化为科技增添温度：5G时代下的科技艺术"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("以文化为本源，以科技为手段，以在建筑、星宿、器玩、戏曲、园林等处处可见的“故宫神兽”身影和故事为主线，内容涵盖中国传统自然观、哲学观、生活方式等，结合当前蓬勃发展的5G通讯、大数据、云计算、物联网、人工智能等前沿技术且受年轻群族追捧的科技手段、沉浸式互动场景体验，用科技艺术唤醒更多年轻国人喜爱传统文化，同时打开不同文明交流的一扇窗，成为新时代中国“文化与科技”的国家名片。 文化和科技深度结合，既催生了新的文化业态、延伸了文化产业链，加速优秀原创的科技艺术内容生产和激发文化创造活力，助力传统文化产业转型升级。同时，又集聚了大量创新人才，将加速通讯、计算机、人工智能等科技人才与人文、艺术、影视等跨学科人才的融合和复合型人才的培养，使科技、文化和商业不再割裂。引领数字文化产业迎来新一轮爆发式增长，培育数字经济新优势，重塑数字技术的文化产业新生态，形成更强大的国家创新活力，触发新一轮经济增长周期。 依托深厚的中华优秀传统文化底蕴和与日俱增的国际科技竞争力，推动社会资源更广泛的融入国家公共文化和先进科技建设，通过“坚守工匠精神，赋能科技温度，引领消费升级”的方式探索科技艺术成为社会发展的新引擎。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("林俊廷，新媒体艺术家，生于台湾·台北，定居上海，现任青鸟新媒体艺术-艺术总监，四川美术学院新媒体艺术系特聘教授、硕士研究生导师，是引领当代华人新媒体艺术风潮的代表性人物。其擅长透过物理三维空间和第四维「时间」的交融呈现，让观者经由身体参与、经历心理知觉变化而完成一种「幻境体验」，在进入现实与非现实的缝隙之际，当下的感知与如梦的体验交互辩证构成一种完形审美经验。曾获美国洛克斐勒基金会ACC亚洲文化协会国巨科技艺术奖，为第一位在纽约Location One科技艺术中心驻村创作的台湾艺术家，作品曾应邀于英国、法国、意大利、芬兰、美国、日本、韩国、新加坡等国际展览中展出，并获美国、澳洲及新西兰等地的美术馆和上海文化发展基金会、国巨基金会……等永久收藏。")]),_c('p',[_vm._v("林俊廷作品以光与影交织的诗意风格和人文气息，藉由和潜意识世界的和谐对话，让人们进入异度的时空漫游，古典轻盈、实拟虚境的手法语汇，激发出人类所共有的视觉记忆或某些潜意识向度，美好神秘的心灵彼岸意象油然再现。2002年，成立青鸟新媒体艺术——一个以光和影造梦的创意实验室，邀集来自视觉艺术、博物馆展示、文学、游戏创意开发、动画、程序、电子电路等不同专业的创意人，专注以当代新媒体重新转译中国文化与美学，不断跨界创作发展新的概念与呈现方式。2011年，与台北故宫博物院合作「山水觉─黄公望与富春山居图新媒体艺术特展」，由不同角度切入诠释《富春山居图》之神思意境，是目前亚洲唯一展览荣获美国博物馆协会(AAM)《谬思奖》MUSE诠释互动金奖(2012)、英国艺术报(The Art Newspaper)评选为当年度全球人气最高的展览第三名。2020年，故宫博物院建成600年之际，林俊廷及青鸟新媒体艺术更将与其携手，以新媒体艺术手法，转化中华民族深厚的传统文化，诠释属于华人的真正内涵，创造属于新时代的文化中国梦。")])])]),_c('hr'),_c('div',{staticClass:"container"},[_c('div',{staticClass:"photo"},[_c('img',{attrs:{"src":require("../assets/images/2/25.png"),"alt":"陈世卿","align":"middle"}})]),_c('div',{staticClass:"content"},[_c('b',[_vm._v("特邀讲者：")]),_vm._v("王海燕，四川美术学院"),_c('br'),_c('b',[_vm._v("报告主题：")]),_vm._v("大足石刻数字化保护研究"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("主要利用深度卷积神经网络、数字图像修复算法、图像融合技术、图像学、美术史学与考古学方法，交叉应用于大足石刻的数字图像识别、虚拟修复与美术考古研究。将计算机领域的定量研究方法应于石窟艺术研究，一方面印证传统的定性研究的合理性，另一方面也对一些传统研究方法无法解决的问题做出有益探索。研究首先提出一种有效的基于VGGNet的大足石刻佛教头像的造型风格识别方法，并对北山180窟观音造像的数量作出辨析；然后改进基于样本的图像修复算法，提出基于样本库生成样本图以完成图像修复的方案，并将之应用于石门山6号窟杨柳观音头像的虚拟修复，在一定程度上可以解决当原图像因破损区域太复杂或过大而无法提供充足先验信息的艺术图像修复问题。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("王海燕，计算机应用方向博士，四川美术学院艺术人文学院教师，硕士研究生导师。研究方向：影像艺术、文化遗产数字化保护。主要致力于大足石刻数字图像识别与虚拟修复研究，主要研究成果有：出版学术专著1部，发表SCI及A&HCI检索、CSSCI检索、北大核心及其他重要学术期刊论文10余篇，主持完成与在建省部级重点项目2项、一般项目2项。")])])]),_c('hr'),_c('h1',{staticStyle:{"font-size":"35px","text-align":"center"}},[_vm._v("民俗与非遗文化报告")]),_c('hr'),_c('div',{staticClass:"container"},[_c('div',{staticClass:"photo"},[_c('img',{attrs:{"src":require("../assets/images/3/31.png"),"alt":"陈世卿","align":"middle"}})]),_c('div',{staticClass:"content"},[_c('b',[_vm._v("特邀讲者：")]),_vm._v("米海鹏，清华大学"),_c('br'),_c('b',[_vm._v("报告主题：")]),_vm._v("“墨甲”中国风机器人乐队：当文化计算遇上艺术机器人"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("机器人艺术是一个新兴的学科交叉领域。在探索机器人的艺术表现形式时，文化因素是不应该被忽视的。“墨甲”机器人乐队最早诞生于清华大学，是一次将中国传统文化与机器人艺术相结合的大胆尝试。这支乐队的三名机器人乐手可以分别演奏三种中国传统乐器。墨甲中国风机器人乐队的设计，运用了多个学科的技术和方法。我们构建了一套数字孪生环境，用于快速地设计多样的机器人角色，以及更加复杂的演出场景。而基于动作捕捉驱动机器人的技术，让乐队的新成员具备了丰富的面部表情表现能力。新的计算技术和方法，让中国风机器人艺术具有了更多的可能性。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("米海鹏，博士，副教授，2017年入选中组部“万人计划”青年拔尖人才，同年荣获清华大学“学术新人奖”。现任教于清华大学美术学院信息艺术设计系，并兼任清华大学未来实验室客座研究员。作为一名跨学科设计领域的青年学者，他拥有多学科的教育背景，以及丰富的跨学科设计研究经历。其多年的设计科研工作致力于探索融合艺术与科学、设计与科技的前瞻性和挑战性的研究课题，是一名勇于实践的创新者。")]),_c('p',[_vm._v("近年来，米海鹏博士致力于探索中国传统文化、传统技艺与新兴交互技术相结合的新的设计可能性。他率先提出了中国风机器人艺术的概念，将中国传统文化与艺术元素与先进的机器人技术相结合，创造了世界上第一支机器人乐队“墨甲”。这一研究工作经过大量媒体报道，在社会上引起广泛关注，曾两次登上热搜，在网上阅读量超过1.2亿次。")]),_c('p',[_vm._v("米海鹏分别于2005年和2008年分别于清华大学获得了理学学士（物理）及工学硕士（电子科学与技术）学位，并于2011年于东京大学获得工学博士（电子信息系统）学位。加入清华美院之前，他曾历任日本科学技术振兴机构研究员、研究推进委员，以及东京大学助理教授等职。")])])]),_c('hr'),_c('div',{staticClass:"container"},[_c('div',{staticClass:"photo"},[_c('img',{attrs:{"src":require("../assets/images/3/32.png"),"alt":"陈世卿","align":"middle"}})]),_c('div',{staticClass:"content"},[_c('b',[_vm._v("特邀讲者：")]),_vm._v("郑国珍，中国文物学会、福建省文物局"),_c('br'),_c('b',[_vm._v("报告主题：")]),_vm._v("福建省文化遗产"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("本报告将就福建省文化遗产的状况做梳理和介绍，具体包括三个部分：世界遗产在福建；国家历史文化名城、名镇、名村与传统村落在福建；全国重点文物保护单位在福建。其中，世界遗产在福建部分还将进一步从五个方面展开：世界文化遗产、自然遗产、文化和自然遗产在福建；世界灌溉工程遗产在福建；全球重要农业文化遗产在福建；世界非物质文化遗产在福建；中国世界遗产预备对象在福建。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("郑国珍，曾任福建省文化厅党组成员、福建省文物局局长，福建省文博系列正高级职称评审委员会主任委员，厦门大学人文学院兼职考古学教授，福建师范大学特聘教授等。现任中国文物学会副会长及古村镇专业委员会主任委员、历史文化街区专业委员会副主任委员，国家文物局文化遗产专家组成员、工程管理专家组成员，国际古迹遗址理事会国际会员，福建省文史研究馆馆员及咨政研究院副院长，福建省理论宣讲专家库首批专家，福建省高校思想政治理论课特聘教授，福建省直机关党工委讲师团特聘教授，福建省历史文化名镇名村和传统村落专家委员会主任，福建省历史文化名城名镇名村与历史文化街区研究中心学术委员会主任，福建省海洋文化研究中心研究员，福建省文物考古博物馆学会会长等。")]),_c('p',[_vm._v("主要研究领域涉及：文物考古博物馆学与文化遗产的保护传承、传统街区和名城名镇名村的保护发展等方面理论探索与实践总结。主持完成《中心城市的传统街区保护》《历史文化名镇（乡）、名村保护现状与发展对策》等国家文物保护科学和技术研究课题；主持编撰出版《福建大观·福建文物》、《中国文物地图集·福建卷》（上下）、《中国音乐大系·福建卷》、《福建省志·文物志》、《福建土楼》、《福建三明万寿岩旧石器时代遗址》、《福建涉台文物图录》（上下）、《福建省革命文物图册》、《顺昌元坑古镇》等数十部著作；撰写发表《闽越文字考》《“永隆通宝”铁钱产生的历史背景及其制作工艺初探》《寻觅历史的踪迹－跨世纪的福建考古》《闽南史前文化初探》《海峡两岸城隍信仰在神缘关系中的地位等近百篇论文；主持编写的20余部微电影剧本中，《刺桐城标•东西塔》获第三届亚洲微电影艺术节“优秀作品奖”和“最佳摄影奖”，第四届华东六省一市暨部分城市微电影（微视频）大赛纪实类一等奖；《魏征•刚直不阿斗权贵》获第四届亚洲微电影艺术节“金海棠奖”；《黄君奕住•厦鼓甘露》和《胡友义的钢琴天堂》获国家广电总局2015全国原创网络视听节目优秀作品；《致我们终将逝去的爱情》获国家广电总局2016年全国原创网络视听节目优秀网络电影；《越过梦山》获2017年福建省第八届百花文艺奖。先后参与主持将：中国“武夷山”作为世界文化与自然遗产，将中国“福建土楼” 、中国厦门“鼓浪屿：历史国际社区”作为世界文化遗产，分别申报列入到《世界遗产名录》；将中国“泉州（刺桐）史迹”（现更名“泉州：宋元中国世界海洋商贸中心”）申报世界文化遗产；将“海上丝绸之路――福州、泉州、漳州史迹”、福州“三坊七巷”、闽南“红砖建筑”、闽东北“木拱廊桥”申报列入到《中国世界文化遗产预备名录》；将福建省域的历史文化名镇名村、历史文化街区、传统村落、文物保护单位申报进入到国家级保护系列等。")])])]),_c('hr'),_c('div',{staticClass:"container"},[_c('div',{staticClass:"photo"},[_c('img',{attrs:{"src":require("../assets/images/3/33.png"),"alt":"陈世卿","align":"middle"}})]),_c('div',{staticClass:"content"},[_c('b',[_vm._v("特邀讲者：")]),_vm._v("佘莹莹，厦门大学"),_c('br'),_c('b',[_vm._v("报告主题：")]),_vm._v("赋能文旅，点亮非遗：提线与掌中木偶表演机器人"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("珍贵的文化遗产是我们祖先智慧、审美、生活情趣的见证，也是中华优秀传统文化的代表。提线与掌中木偶机器人新媒体交互表演与推广项目是传统闽南木偶表演与跨媒体艺术、现代科技的结合。项目秉承了“非遗保护与传承”与“艺术创新与推广”的理念，基于“以人为本”的核心思想，针对闽南木偶及其发展现状进行了充分的实地考察与调研，尝试利用现代技术，来解决制约传统木偶艺术表演进行传播推广的瓶颈问题，实现木偶艺术与现代科学技术的深度融合。")]),_c('p',[_vm._v("项目立足于非遗文化，以及孕育其发展的悠久闽南历史与文化，通过自主研发的木偶表演机器人以及互动媒体艺术、互联网等多学科交叉手段，让观众不仅能够融入到虚实结合的木偶表演现场，还能够获得沉浸式体验，感受古代丝绸之路以及闽南文化的多元魅力。交互表演以闽南古建筑作为舞台，基于木偶传统演出剧目，开创性地构建了机器人、演员与观众三者互动的表演模式。这样新颖的多元化表演模式，有效地吸引国内外各年龄层观众，让公众产生价值认同和情感共鸣。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("佘莹莹，博士，副教授，现任教于厦门大学信息学院软件工程系。近年来，佘莹莹博士致力于非遗文化的科技创新之路，探索跨媒体艺术与智能在非遗传承与发展中所起的作用。她与来自于信息、艺术、人文、非遗传承人等组成跨学科团队研发的世界首创的提线木偶与掌中木偶表演机器人，将机器人技术与闽南传统非遗表演进行融合。团队与非遗木偶表演联合不仅前往美国、日本、新加坡等国家，也进入大、中、小学校，将最传统的掌中木偶文化艺术与最前瞻的科技创新展示给国内外的青少年，引发人们的文化共鸣。这一研究工作获得了中国国家艺术基金的资助，也吸引了大量海内外媒体报道，获得了多项国家级创新奖励，并于2020年度获得中国文化部“文化和旅游信息化发展典型案例”。")]),_c('p',[_vm._v("佘莹莹于2006年和2011年分别于加拿大Concordia大学获得了计算机科学硕士及哲学博士（计算机科学）学位，并于2011年进入厦门大学担任助理教授、副教授至今。")])])]),_c('hr'),_c('div',{staticClass:"container"},[_c('div',{staticClass:"photo"},[_c('img',{attrs:{"src":require("../assets/images/3/34.png"),"alt":"陈世卿","align":"middle"}})]),_c('div',{staticClass:"content"},[_c('b',[_vm._v("特邀讲者：")]),_vm._v("祁春英，中央民族大学"),_c('br'),_c('b',[_vm._v("报告主题：")]),_vm._v("激活传统、创新未来一一中国少数民族传统服饰图案解读"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("中国少数民族服饰有着一山不同天，一里不同服的说法各民族服饰千姿百态的款式、巧夺天工的技艺，尤其寓意深刻的服饰传统图案将各民族的历史、宗教、迁徒路线、图腾崇拜等把服饰文化内涵体现的淋漓尽致！而且古朴、独特，凝聚着民族的精神与情感！在国家当下非物质文化遗产保护中，时尚服饰的创新设计中都有着十分重要的历史意义！")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("祁春英，本科、硕士分别毕业于中央民族大学历史系民族史及专门史专业，现任中央民族大学少数民族服饰研究所所长，研究员，硕士生导师。")]),_c('p',[_vm._v("一、主要业绩：2002年11月在中国国际时装周《民族魂——少数民族服饰专场发布会》荣获组委会颁发的“中国服饰文化贡献奖”；2009、2019年分别担任中华人民共和国“国庆六十周年、七十周年大庆”民族服饰设计、制作艺术总监。并获得游行指挥部颁发的“突出贡献专家”荣誉证书；2011年5月担任西双版纳傣族大型服饰晚会“穿在身上的历史”总导演和服饰品牌“水韵.娑罗”创始人；2013年担任青海省海南藏族自治州六十周年庆典《轮回中的印记》总导演；2016年起担任文化部 “锦绣中华---中国非遗服饰秀”专家委员会副主任；2018年-2021年被光明日报聘请为“中国非遗大使”推广专家委员会专家；2018年被北京服装学院聘为“中国传统工艺振兴计划协同创新中心传统服装服饰研究所所长”；2018年荣获中纺业联 “首创杯”2018年度这个纺织非遗推广大使；2019年被聘为“全国少数民族服饰传承保护标准化技术委员会”专家委员；2019年被福建省文化厅聘请为（2019.9-2024.9）福建省服装创意设计中心第一届学术委员会委员。")]),_c('p',[_vm._v("二、出版的主要著作：专著《中国少数民族头饰》；专著《中国少数民族婚俗》；专著《中国少数民族服饰文化研究》；专著《中国最美——民族服饰》")]),_c('p',[_vm._v("三、主要国际文化交流：2000年11月“中国文化美国行”，2001年11月“中国文化德国行”活动中分别担任少数民族服饰设计、制作与总顾问；2003年9月前往匈牙利佩奇镇进行民族服饰文化交流；2006年7月前往韩国女子学院，进行中韩服装服饰文化交流；2011年9月前往日本松山嫒圆大学进行中国少数民族服饰展览及文化交流；2013年9月应国家民族事务委员会邀请，前往波兰参加中国波兰文化周活动。和华沙民俗博物馆进行民族服饰文化交流及讲座；2018年9月应文化部、国家民委文化交流项目前往柬埔寨金边作\"羌族服饰”的文化交流。")])])]),_c('hr'),_c('div',{staticClass:"container"},[_c('div',{staticClass:"photo"},[_c('img',{attrs:{"src":require("../assets/images/3/35.png"),"alt":"陈世卿","align":"middle"}})]),_c('div',{staticClass:"content"},[_c('b',[_vm._v("特邀讲者：")]),_vm._v("李凯勇，青海民族大学"),_c('br'),_c('b',[_vm._v("报告主题：")]),_vm._v("面向青绣文化保护与传承的创意设计方法研究"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("“青绣”作为宣传青海的新名片，汇集了青海地区多个少数民族非物质文化遗产的结晶，成为青海最具代表性的文化符号。现有的青绣数字化保护以数字采集为主，缺乏元素提取、构图分析等研究，“只看不用”成了阻碍青绣产业发展的主要瓶颈；局限于传统题材，设计过于陈旧，难以适应新时代的发展理念和大众的个性化需求；图案设计严重依赖非遗传人的经验，但又缺乏针对青绣的计算机辅助设计方法，通用设计软件效率低、智能化程度较差，无法支持青绣的创意设计。如何促进非物质文化遗产的不断传承与持续性发展、探索中国特色文化遗产传承利用创新服务模式，具有维护文化多样性的固有功能和促进经济发展的工具性作用两方面的发展价值。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("李凯勇，长期从事文化计算和人工智能方面技术与非物质文化遗产的数字化研究工作，已初步完成青海“堆绣”图案的数据挖掘图像特征分割、大区域图像局部破损点优化提取、监督性机器学习算法的图像辅助去噪、机器学习的光照不均图像边缘检测系统等相关前期工作。")])])]),_c('hr'),_c('h1',{staticStyle:{"font-size":"35px","text-align":"center"}},[_vm._v("文化产业与推广报告")]),_c('hr'),_c('div',{staticClass:"container"},[_c('div',{staticClass:"photo"},[_c('img',{attrs:{"src":require("../assets/images/4/41.png"),"alt":"陈世卿","align":"middle"}})]),_c('div',{staticClass:"content"},[_c('b',[_vm._v("特邀讲者：")]),_vm._v("陆地，北京大学"),_c('br'),_c('b',[_vm._v("报告主题：")]),_vm._v("视频满意度评估与大数据分析"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("传统上，视频节目的评估主要依靠收视率调查和点击量或下载量。2010年起，北京大学视听传播研究中心结合大数据技术和语义分析技术，创立了一套以满意度为核心指标、以新媒体用户为调查对象、定量研究与定性研究相结合的视频内容价值评估体系，连续进行了九次中国电视满意度调查、七次中国网络视频满意度调查和三次中国网络视频精品满意度调查暨发布，有力地纠正了节目评估“唯收视率”的弊端，为中国视频内容市场的多维度评估和市场调查方法的与时俱进以及视频产业的健康发展，提供了更多的选择，取得了良好的社会效益，获得了视频行业的广泛认可。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("陆地，中国人民大学新闻学博士，中国首位新闻传播学博士后（复旦大学）。曾任中国青年报、北京电视台等新闻媒体的记者、主任编辑和清华大学新闻与传播学院教授。现任北京大学新闻与传播学院教授、博士生导师，北京大学视听传播研究中心主任，兼任中国农工民主党中央文化委员会副主任、中国大众文化学会节庆礼仪研究会会长、中广联广播电视研究会副会长、北京市写作学会会长。1998年以来，发表广播电视与新媒体以及文化产业等方面的论文350余篇，出版专著、编著、译著等19部；2004年被中国广播电视协会评选为全国“十佳”广播电视理论工作者，2019年受聘“巴渝学者”。国家社科基金重大项目《周边传播理论与应用研究》首席专家")])])]),_c('hr'),_c('div',{staticClass:"container"},[_c('div',{staticClass:"photo"},[_c('img',{attrs:{"src":require("../assets/images/4/42.png"),"alt":"陈世卿","align":"middle"}})]),_c('div',{staticClass:"content"},[_c('b',[_vm._v("特邀讲者：")]),_vm._v("陈学会，中国动漫集团"),_c('br'),_c('b',[_vm._v("报告主题：")]),_vm._v("数字文化产业发展理论与实践探索"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("数字创意产业成为国家战略性新兴产业，而其核心的数字文化产业日益受到政府和市场的高度关注，成为社会经济发展的新引擎、新动力。数字技术在文化产业的应用以出乎人们预料的效果，助力文化产业的快速、高效发展。数字技术与文化创作的天然耦合性，使得文化产品的创作不仅富产而且高产同时也拉平了一般创作生产的差距，并极大地提高了文化传播效率，推动了文化产业的快速发展。数字技术、特别是文化计算、大数据、人工智能、新媒体等新技术在文化产业的广泛、深入运用，导致传统文化产业理论和实践的不适宜，文化产业政策指导性失灵。因此，就必须在新技术与文化融合、即“文化+科技”条件下，对文化产业发展理论进行探索和创新，才能制定适应市场需要的政策，才能指导市场主体实践突破，才能有效促进文化产业健康、快速、高质量发展。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("陈学会，南开大学经济学博士，高级经济师、研究员。现任中国动漫集团有限公司党委委员、董事、副总经理。曾任中央企业、大型国有企业、上市公司、投资公司等高管职务。中国大众文化学会文化旅游专业委员会会长，北京市服务贸易协会副理事长。被聘为北京大学视听传播研究中心，南开大学滨海开发研究院，北京邮电大学世纪学院、北京电影学院动画学研究院、西南政法大学新媒体研究院、首都经贸大学会计学院等研究员、客座教授、学术委员，硕士生导师。被聘为国家艺术基金评审专家、科技部科技项目评审专家、文化和旅游部项目评审专家。中国动漫“新光奖”、中国国际新媒体短片节等终评委。")]),_c('p',[_vm._v("主导创作出品的原创3D动画片《彩虹宝宝》，已在70余个国家和地区电视台及腾讯、爱奇艺等互联网平台播出。作为主编、策划、制片人、监制等打造了《中国动漫春节联欢晚会》、《二十四节气》绘本、《五行传奇》、《龙行天下》、《菩提树》、《少年老子》、《二十节气密码》、《丝路游记》等动画、游戏、漫画产品。主导举办四届《中国数字艺术产业高峰论坛》。")])])]),_c('hr'),_c('div',{staticClass:"container"},[_c('div',{staticClass:"photo"},[_c('img',{attrs:{"src":require("../assets/images/4/43.png"),"alt":"陈世卿","align":"middle"}})]),_c('div',{staticClass:"content"},[_c('b',[_vm._v("特邀讲者：")]),_vm._v("汪大海，北京师范大学"),_c('br'),_c('b',[_vm._v("报告主题：")]),_vm._v("周期汉字：汉字学习与汉字计算的革命"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("以汉字周期表和汉字科学编码与解码等原始创新底层技术为基础，围绕“新汉语：语文何以教育；新计算：汉字何以计算”，促进汉语言学 、信息科学、脑与认知科学、教育学等相关学科交叉研究与技术创新，牵头制定首个周期汉字科学学习国际标准和首个周期汉字计算国际技术标准，打造周期汉字创新链和产业链，催生具有中国特色、中国风格、中国气派的下一代汉字计算关键共性技术。最终实现“文化自信从汉字自信开始，汉字自信从汉字计算出发”的愿景。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("汪大海，北京师范大学二级教授、博士生导师，北京师范大学汉字科学研究中心主任。作为项目负责人先后主持国家哲学社会科学基金重大项目、重点项目、欧盟/联合国开发计划署援助项目、教育部、民政部、文化部等项目30余项。出版著作、教材20余部，发表学术论文近百篇。多份研究报告获得李克强总理等国家和省部级领导的重要批示，相关政策建议被有关部委采用。")])])]),_c('hr'),_c('div',{staticClass:"container"},[_c('div',{staticClass:"photo"},[_c('img',{attrs:{"src":require("../assets/images/4/44.png"),"alt":"陈世卿","align":"middle"}})]),_c('div',{staticClass:"content"},[_c('b',[_vm._v("特邀讲者：")]),_vm._v("才武杰，北京中科视维文化科技有限公司"),_c('br'),_c('b',[_vm._v("报告主题：")]),_vm._v("面向大众数字文旅消费基于5G的沉浸性交互光影夜游"),_c('br'),_c('b',[_vm._v("报告摘要：")]),_c('p',[_vm._v("利用数字虚拟视觉结合智能人机交互，基于5G技术的一体化平台，打造可观、可游、可玩等沉浸性互动光影幻境，将所在地打造成文化与科技、视觉与交互、线上与线下、体验与消费多维度融合的全新夜游文旅地标与夜晚数字旅游消费目的地。在形成核心游览产品的同时，也带动区域夜间餐饮、零售、住宿等商业经济的提升。")]),_c('b',[_vm._v("个人简介：")]),_c('p',[_vm._v("才武杰，北京中科视维文化科技有限公司总裁助理兼规划总监。第一批数字化教育的探索者。2012年加入团队，作为核心人员参与重点国家课题以及商业项目的规划工作。目前带领团队负责项目与产品的整体规划与设计，规划策划领域包括：特色小镇、产业园区建设与运营；科技文旅景园、创新性场馆、主题公园、智慧文旅平台等科技文旅项目建设与运营；数字艺术展等展览展示布展与运营；生命科学、远古生命以及传统文化等方面的科学可视化工程建设等等。落地案例包括2019年北京世园会“智慧世园”项目与“奇幻光影森林”项目、江西定南工业视觉基地等等。")])])]),_c('hr')])}]

